import React, { Component } from 'react';
import ScaleText from 'react-scale-text';
var logoimg =require( './../../graphics/at.svg');

class Header extends Component {
  render() {
    return (
  <div className="App">
  
  <header>

  <ScaleText>
  <div className="logo">
 <img src={logoimg} height="60px" alt="Amicus"/>

  </div>

  <nav>
  <ul>
  <li className="first">
  <a href = "#">Home</a>
  </li>
  <li>
  <a href = "#">Products</a>
  </li>
  <li className="last">
  <a href = "#">Contracts</a>
  </li>
  
  
  </ul>
  </nav>

  </ScaleText>

  
  </header>
  
  
  
  </div>
    );
  }
}

export default Header;



