import React, { Component } from 'react';



class BlgPost extends Component {
  render() {
    return (

<div className="post-wrap">

<center>
<p></p>
<p></p>
<div className="card rounded ">
<p>
<p>
</p>
</p>
<h1 className="h1bpost"> {this.props.name}</h1>
<p></p>
<img className="img-fluid  rounded" height="30px" src={this.props.source}></img>
<p></p>
<p>{this.props.section}</p>
</div>



</center>



</div>
  
 
  
  
    );
  }
}

export default BlgPost;



