import React, { Component } from 'react';



class Profile extends Component {
  render() {
    return (

<div className="post-wrap">

<center>
<p></p>
<p></p>

<div className="card profile-wrap ">
<p>
<p>
</p>
</p>
<div className="card-title">
<h1 className="profile-name">{this.props.name}</h1></div>
<p></p>

<img className="responsive-img profile-img" src={this.props.source}></img>

<p></p>
<div class="card-block">

<p className="profile-username card-subtitle mb-2 text-muted">Username: {this.props.username}</p>

<div className="bio-container">
<p className="profile-bio">{this.props.bio}</p>
</div>

<p className="profile-sessions">Sessions: {this.props.sessions}</p>

</div>
</div>



</center>



</div>
  
 
  
  
    );
  }
}

export default Profile;



